.about-company{
    padding-top: 10px;
    padding-bottom: 20px;
    background-color: rgba(0,0,0,.16);
}

.about-company-headline{
    margin-bottom:10px;
    color: #004a87;
}

.about-company__content{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
    color: #004a87;
}

.about-company__image{
    width: 30%;
    margin-bottom: 10px;
}

.about-company__text-content-half{
    max-width: 60%;
    margin-right: 5%;
}

.about-company__text{
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
}

.about-company__text-list{
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
    list-style-type: circle;
    margin-left: 35px;
}

.about-company .about-company__content:nth-child(3) .about-company__image{
    width: 40%;
    margin-right: 5%;
}

.about-company .about-company__content:nth-child(3) .about-company__text-content-half{
    max-width: 50%;
    margin-right: 0;
}