.button{
    text-align: center;
    width: 100px;
    height: 40px;
    border-radius: 15px;
    background-color: #e73b2c;
    color: #fff;
    font-family: OpenSans-Bold,sans-serif;
    font-weight: 700;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button:hover{
    background-color: #f50a0a;
}