.social-links{
    display:flex;
    width: 180px;
    justify-content: space-between;
}

.social-link{
    height:32px;
    width: 32px;
    border-radius: 50%;
}

.social-link__image{
    width: 100%;
    height: 100%;
}

.social-link:hover >.social-link__image path{
    fill:#f50a0a;
}

.social-link:hover > .social-link__image circle{
    fill:#f50a0a;
}

.social-link__image circle{
    transition: fill 0.3s;
}

.social-link__image path{
    transition: fill 0.3s;
}
